.Pokecard {
  width: 300px;
  margin: 1rem;
  padding: 10px;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.5);
  text-align: center;
  background-color: white;
  border-radius: 10px;
}
.Pokecard-name {
  font-weight: 700;
}
.Pokecard-status {
  font-weight: 250;
}

.Pokecard-data {
  font-size: 100%;
  margin-top: 7px;
}

.winningClass {
  color: green;
}
.losingClass {
  color: red;
}

.winningImgClass {
  background-color: rgba(0, 177, 56, 0.708);
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  margin-left: 10px;
  margin-right: 10px;
}
.losingImgClass {
  background-color: rgba(177, 0, 0, 0.708);
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  margin-left: 10px;
  margin-right: 10px;
}
.Pokecard img {
  transition: all ease 500ms;
}
.Pokecard img:hover {
  transform: scale(1.1);
  opacity: 0.8;
}
