.Pokegame-container {
  display: flex;
  flex-flow: column nowrap;
  min-height: 100vh;
}
.randomizeButton {
  border: none;
  background-color: #ffa305;
  padding: 40px 30px;
  color: black;
  font-size: 25px;
  font-family: "Gluten", cursive;
}

.randomizeButton:hover {
  color: rgb(140, 140, 140);
  cursor: pointer;
  transition: all 400ms ease-out;
  transform: scale(1.1);
}
.Pokegame-cards {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
